import { Modal } from 'antd';
import React from 'react';
import Option1 from './option1';
import Option2 from './option2';
import Option3 from './option3';
import Option4 from './option4';
import Option5 from './option5';
import Option6 from './option6';
import Option7 from './option7';
import Option8 from './option8';


class OptionModal extends React.Component {
  state = {
    modal1Visible: false,
  };

  titles = ['View Current Balance of the account', ' Recharge Account', 'View available packs, channels and services', 'Subscribe to base packs', 'Add channels to an existing subscription', 'Subscribe to special services', 'View current subscription details', 'Update email and phone number for notifications']

  setModal1Visible(modal1Visible) {
    if(!modal1Visible){
      this.props.onCancel();
      this.setState({ modal1Visible: false });
    } else {
      this.setState({ modal1Visible: true });
    }
  }

  componentDidMount() {
    this.setState({ modal1Visible: true });
  }

  getCurrentBalance() {
    return `<div><p>your current balance is 100</p></div>`
  }

  render() {
    return (
      <div>
        <Modal
          title= {this.titles[this.props.option - 1]}
          centered
          footer={null}
          visible={this.state.modal1Visible}
          onOk={() => this.setModal1Visible(false)}
          onCancel={() => this.setModal1Visible(false)}
          maskClosable= {false}
        >
          {this.props.option === 1 &&
          <Option1></Option1>
          }
          {this.props.option === 2 &&
          <Option2 onCancel={this.props.onCancel}></Option2>
          }
          {this.props.option === 3 &&
          <Option3></Option3>
          }
          {this.props.option === 4 &&
          <Option4 onCancel={this.props.onCancel}></Option4>
          }
          {this.props.option === 5 &&
          <Option5 onCancel={this.props.onCancel}></Option5>
          }
          {this.props.option === 6 &&
          <Option6 onCancel={this.props.onCancel}></Option6>
          }
          {this.props.option === 7 &&
          <Option7></Option7>
          }
          {this.props.option === 8 &&
          <Option8 onCancel={this.props.onCancel}></Option8>
          }
        </Modal>
      </div>
    );
  }
}

export default OptionModal;

