import React from 'react';
import '../App.css';
import OptionModal from './modal';
import { List, Typography, notification } from 'antd';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      openModal: false,
      option: ''
    };
    this.changeOption = this.changeOption.bind(this);
    this.onCancel = this.onCancel.bind(this);
  }

  changeOption = (option) => {
    this.setState({
      openModal: true,
      option
    });
  }

  onCancel = () => {
    this.setState({
      openModal: false
    });
  }

  logout = () => {
    localStorage.removeItem('token');
    notification.open({
      message: 'Logged out Successfully.',
    });
    window.location.reload();
  }

  render() {
    return (
      <div className="App">
      <header className="App-header">
          <div>Welcome to SatTV</div>
        <div style={{alignContent:"left"}}>
          <List.Item>
            <Typography.Text code onClick={() => this.changeOption(1)}>1. View Current Balance of the account</Typography.Text>
          </List.Item>
          <List.Item>
            <Typography.Text code onClick={() => this.changeOption(2)}>2. Recharge Account</Typography.Text>
          </List.Item>
          <List.Item>
            <Typography.Text code onClick={() => this.changeOption(3)}>3. View available packs, channels and services</Typography.Text>
          </List.Item>
          <List.Item>
            <Typography.Text code onClick={() => this.changeOption(4)}>4. Subscribe to base packs</Typography.Text>
          </List.Item>
          <List.Item>
            <Typography.Text code onClick={() => this.changeOption(5)}>5. Add channels to an existing subscription</Typography.Text>
          </List.Item>
          <List.Item>
            <Typography.Text code onClick={() => this.changeOption(6)}>6. Subscribe to special services</Typography.Text>
          </List.Item>
          <List.Item>
            <Typography.Text code onClick={() => this.changeOption(7)}>7. View current subscription details</Typography.Text>
          </List.Item>
          <List.Item>
            <Typography.Text code onClick={() => this.changeOption(8)}>8. Update email and phone number for notifications</Typography.Text>
          </List.Item>
          <List.Item>
            <Typography.Text code onClick={() => { this.logout() }}>9. Exit</Typography.Text>
          </List.Item>
          {this.state.openModal &&
            <OptionModal option={this.state.option} onCancel={this.onCancel}></OptionModal>
          }
        </div>
        </header>
      </div>
    );
  }
}

export default Home;
