import { Input, Button, notification } from 'antd';
import React from 'react';

class Option2 extends React.Component {
  constructor() {
    super();
    this.state = {
      amount: 0,
    }
    this.onSubmit = this.onSubmit.bind(this);
  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  onSubmit() {
    const { amount } = this.state;
    const token = localStorage.getItem('token');
    const regex = /^[0-9]+$/;

    if (!amount.match(regex) || amount <= 0) {
      return alert('enter valid amount');
    }
    fetch('http://localhost:5000/api/recharge', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify({ amount: parseInt(amount) })
    })
      .then(response => response.json())
      .then(data => {
        notification.open({
          message: 'Recharge successful.',
        });
        this.props.onCancel();
      })
      .catch(err => console.log(err));
  }

  render() {
    return (
      <div className="App">
        <Input onChange={this.handleChange} name='amount' placeholder="Enter Amount" />
        <Button style={{ marginTop: '10px' }} type="primary" onClick={this.onSubmit}>Recharge</Button>
      </div>
    );
  }
}

export default Option2;