import React from 'react';

class Option7 extends React.Component {
  constructor(){
    super();
    this.state = { 
      subscription: {},
    }
  }

  componentDidMount() {
    this.getCurrentSubscription();
  } 

  getCurrentSubscription() {
    const token = localStorage.getItem('token')
    fetch('http://localhost:5000/api/subscription', {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization':`Bearer ${token}`
      }
    })
      .then(response => response.json())
      .then(data => {
        this.setState({ subscription: data});
      })
      .catch(err => console.log(err));
  }

  render() {
    return (
      <div>
        <p>Active Subscription: {this.state.subscription.activeSubscription ? this.state.subscription.activeSubscription : `No Active Subscription`}  </p>
        <p>Active Services: {this.state.subscription.activeServices ? this.state.subscription.activeServices : `No Active Services`}  </p>
      </div>
    );
  }
}

export default Option7;