import { Select, Button, notification } from 'antd';
import React from 'react';
const { Option } = Select;

class Option5 extends React.Component {
  constructor(){
    super();
    this.state = { 
      channels: [],
    }
    this.onSubmit = this.onSubmit.bind(this);
  }

  onSubmit() {
    const { channels } = this.state;
    const token = localStorage.getItem('token');
    if(!channels.length) {
      return alert('please select one of the packs.')
    }

    fetch('http://localhost:5000/api/addChannels', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization':`Bearer ${token}`
      },
      body: JSON.stringify({ channels })
    })
      .then(response => response.json())
      .then(data => {
        notification.open({
          message: 'Channel added. Email notification sent successfully.',
        });
        this.props.onCancel();
      })
      .catch(err => console.log(err));
  }

  handleChange = (e) => {
    this.setState({
        channels: e
    })
  }

  render() {
    return (
      <div className="App">
        <Select
          mode="multiple"
          placeholder="Please select"
          onChange={this.handleChange}
          style={{ width: '100%' }}
        >
          <Option key="Discovery">Discovery</Option>
          <Option key="Net-Geo">Net Geo</Option>
        </Select>
        <Button className="marginTop" type="primary" onClick = {this.onSubmit}>Add Channels</Button>
      </div>
    );
  }
}

export default Option5;