import { List, Card } from 'antd';
import React from 'react';

class Option2 extends React.Component {
  constructor() {
    super();
    this.state = {
      subscriptions: {},
    }
  }

  componentDidMount() {
    this.getAvailableSubscriptions();
  }

  getAvailableSubscriptions() {
    const token = localStorage.getItem('token')
    fetch('http://localhost:5000/api/subscriptions', {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      }
    })
      .then(response => response.json())
      .then(data => {
        this.setState({ subscriptions: data.subscriptions });
      })
      .catch(err => console.log(err));
  }

  render() {
    return (
      <div>
        <Card title="Available packs for subscription">

          <List
            itemLayout="horizontal"
            dataSource={this.state.subscriptions && this.state.subscriptions['Packs']}
            renderItem={item => (
              <List.Item.Meta
                title={item.PackName}
                description={<p>Price: {item.Price} Rs. Channels: {item.Channels.join(', ')}</p>}
              />
            )}
          />
        </Card>

        <Card className="marginTop" title="Available channels for subscription">
          <List
            itemLayout="horizontal"
            dataSource={this.state.subscriptions && this.state.subscriptions['Channels']}
            renderItem={item => (
              <List.Item.Meta
                title={item.ChannelName}
                description={
                  <p>Price:{item.Price}</p>
                }
              />
            )}
          />
        </Card>

        <Card className="marginTop" title="Available packs for subscription">
          <List
            itemLayout="horizontal"
            dataSource={this.state.subscriptions && this.state.subscriptions['Services']}
            renderItem={item => (
              <List.Item.Meta
                title={item.ServiceName}
                description={<p>Price: {item.Price}</p>}
              />
            )}
          />
        </Card>
      </div>
    );
  }
}

export default Option2;