import { Input, Button, notification } from 'antd';
import React from 'react';

class Option8 extends React.Component {
  constructor() {
    super();
    this.state = {
      enter: false,
      email: '',
      phone: ''
    }
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount(){
    this.getUser();
  }

  getUser() {
    const token = localStorage.getItem('token')
    fetch('http://localhost:5000/api/user', {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization':`Bearer ${token}`
      }
    })
      .then(response => response.json())
      .then(data => {
        this.setState({ email: data.email, phone: data.phone});
      })
      .catch(err => console.log(err));
  }

  onSubmit() {
    const token = localStorage.getItem('token');
    const { email, phone } = this.state;
    if (!email && !phone) {
      alert('Both fields cannot be empty');
    }
    const obj= {};
    if(email) {
      obj.email = email;
    }
    if(phone) {
      obj.phone = phone;
    }
    fetch('http://localhost:5000/api/user', {
      method: 'PUT',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization':`Bearer ${token}`
      },
      body: JSON.stringify(obj)
    })
      .then(response => response.json())
      .then(data => {
        notification.open({
          message: 'User updated.',
        });
        this.props.onCancel();
        if(data.token) {
          localStorage.setItem('token', data.token);
        }
      })
      .catch(err => console.log(err));
  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }
  render() {
    return (
      <div className="App">
        <Input onChange={this.handleChange} name='email' placeholder="Enter Your Email" value={this.state.email}/>
        <Input className="marginTop" onChange={this.handleChange} name='phone' placeholder="Enter Your Phone Number" value={this.state.phone} />
        <Button className="marginTop" type="primary" onClick={this.onSubmit}>Update</Button>
      </div>
    );
  }
}

export default Option8;