import { Radio, InputNumber, Button, notification } from 'antd';
import React from 'react';

class Option4 extends React.Component {
  constructor(){
    super();
    this.state = { 
      pack: '',
      months:1
    }
    this.onSubmit = this.onSubmit.bind(this);
  }

  onSubmit() {
    const { pack, months } = this.state;
    const token = localStorage.getItem('token');
    if(!pack) {
      return alert('please select one of the packs.')
    }

    fetch('http://localhost:5000/api/subscribePack', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization':`Bearer ${token}`
      },
      body: JSON.stringify({ packName: pack, months })
    })
      .then(response => response.json())
      .then(data => {
        notification.open({
          message: 'Base pack subscribed.',
        });
        this.props.onCancel();
      })
      .catch(err => console.log(err));
  }

  handleChange = (e) => {
    this.setState({
        [e.target.name]: e.target.value
    })
  }
  
  render() {
    return (
      <div>
        <div>
        Select Pack:&nbsp;
        <Radio.Group name="pack" buttonStyle="solid" onChange={this.handleChange}>
          <Radio.Button value="Gold">Gold</Radio.Button>
          <Radio.Button value="Silver">Silver</Radio.Button>
        </Radio.Group>   
        </div>     
        <div className="marginTop">
        Select Months:&nbsp;
        <InputNumber min={1} name='months' onChange={(e) => this.setState({months: e})} />
        </div>
        <Button className="marginTop" type="primary" onClick = {this.onSubmit}>Subscribe</Button>
      </div>
    );
  }
}

export default Option4;