import React from 'react';
import Home from './components/home';
import { Input, Button, notification } from 'antd';

class App extends React.Component {
  constructor(){
    super();
    this.state = { 
      enter: false,
      email:'',
      phone:''
    }
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
    const token = localStorage.getItem('token')
    if(token) {
      this.setState({
        enter: true
      })
    }
  }

  onSubmit() {
    const {email, phone} = this.state;
    if(!email) {
      return alert('email required');
    }
    fetch('http://localhost:5000/api/enter', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ email, phone})
    })
      .then(response => response.json())
      .then(data => {
        notification.open({
          message: 'Login successful.',
        });
        this.setState({ enter: true});
        localStorage.setItem('token', data.token);
      })
      .catch(err => console.log(err));
  }

  handleChange = (e) => {
    this.setState({
        [e.target.name]: e.target.value
    })
}

  render() {
    return (
      <div className="App">
      <header className="App-header">
          {!this.state.enter ? <div><div>Welcome to SatTV</div>
          <Input onChange={this.handleChange} name='email' placeholder="Enter Your Email" />
          <Input onChange={this.handleChange} name='phone' placeholder="Enter Your Phone Number" />
          <Button type="primary" onClick = {this.onSubmit}>Enter</Button></div> : <Home></Home>}
      </header>
    </div>
    );
  }
}

export default App;