import { Radio, Button, notification } from 'antd';
import React from 'react';

class Option6 extends React.Component {
  constructor(){
    super();
    this.state = { 
      serviceName: null,
    }
    this.onSubmit = this.onSubmit.bind(this);
  }

  onSubmit() {
    const { serviceName } = this.state;
    const token = localStorage.getItem('token');
    if(!serviceName) {
      return alert('please select one of the packs.')
    }

    fetch('http://localhost:5000/api/subscribeService', {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'Authorization':`Bearer ${token}`
      },
      body: JSON.stringify({ serviceName })
    })
      .then(response => response.json())
      .then(data => {
        notification.open({
          message: 'Services added. Email notification sent successfully.',
        });
        this.props.onCancel();
      })
      .catch(err => console.log(err));
  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value
  })
  }
  render() {
    return (
      <div className="App">
        <Radio.Group name="serviceName" buttonStyle="solid" onChange={this.handleChange}>
          <Radio.Button value="LearnEnglish">LearnEnglish</Radio.Button>
          <Radio.Button value="LearnCooking">LearnCooking</Radio.Button>
        </Radio.Group>        
        <Button className="marginTop" type="primary" onClick = {this.onSubmit}>Add Services</Button>
      </div>
    );
  }
}

export default Option6;